.btn{
    position: relative;
    cursor: pointer;

    width: 200px;
    height: 70px;

    border-radius: 4px;
    border-style: none;

    padding: 17.5px 41.5px;
    transition: all 500ms ease;

    background-color: #ff7c5b;
    
    color: white;
    z-index: 100;
    overflow: hidden;
}

.btn:hover{
    background: #061a3a;
}

.btn:before{
    transition-duration: 800ms;
    content: '';
    position: absolute;

    width: 300px;
    height: 200px;

    top:100px;

    transform: translateX(-130px);
    background-color: #061a3a;

    border-radius: 50%;
    z-index: -1;
}

.btn:hover::before{
    top:-40%;
}