.contanier{
    overflow: hidden;
    width: 0;
    white-space: nowrap;
    /* background-color: rgb(24, 157, 119); */

    animation: shrink 1s linear forwards;
}

.ad-text-andimation{
    animation  : ad-text-andimation-t 1s linear forwards;
}

@keyframes ad-text-andimation-t {
    from { transform: translateY(0); }
    to { transform: translateY(-200px); }
}

@keyframes shrink{
    100%{
        width: 12%;
    }
}