.toast-container{
    position: absolute;

    display: flex;
    flex-direction: column;

    top: 30px;
    right: 100px;
}

.toast{
    position: relative;
    border-radius: 2px;
    box-shadow: 0 0 1px 0 grey;    
    width: 300px;
    height: 50px;
    margin: 10px;
    align-content: center;
}

.toast::after{
    content: '';
    position: absolute;

    left: 0;
    bottom: 0;
    width: 100%;

    height: 5px;
    background-color: rgb(24, 157, 119);

    animation: linedraw 4s linear forwards;
}

.success{
    background-color: greenyellow;
}

.fail{
    background-color: red;
}

@keyframes linedraw{
    100%{
        width: 0;
    }
}
