body{
    margin: 0;
}

.card-container{
    color: black;

    display: flex;
    justify-content: center;
    align-items: center;
    
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    
    z-index: 1000;
    background-color: rgba(0, 0, 0, 0.5);

    overflow: hidden;
}

.card{
    background-color: white;
}