.scroll-container{
    width: 100%; /* Width of the container */
    white-space: nowrap; /* Prevents line breaks inside the container */
    overflow: scroll; /* Adds a horizontal scrollbar when content overflows */
    scrollbar-width:none;
    padding: 10px;
}

.box-inside{
    display: inline-block;
    width: 600px;
    height: 100px;
    padding: 10px;
    margin: 10px;
    background-color: aqua;
    border-radius: 4px;
}