.navbar{
    width: 100%;
    height: 50px;
    
    position: fixed;
    background-color: rgb(238, 138, 8);

    z-index: 11;

    color: white;
}

.sidebar-app {
    display: flex;
    flex-direction: row;

    width: 100%;
    padding-top: 50px;
}

.sidebar-right-content{
    padding-top: 51px;
    padding-left: 150px;
    width: 100%;
    height: 86vh;

    margin-left: auto;
    margin-right: auto;

    background-color: whitesmoke;

    display: flex;
    justify-content: center;
    align-content: center;
}


.nav-left{
    position: absolute;

    left: 10px;
    top: 10px;

    cursor: pointer;
}

.nav-right{
    display: flex;
    gap: 10px;

    position: absolute;

    right: 10px;
    top: 10px;

    cursor: pointer;
}