.main-container{
    display: flex;
    justify-content: center;
    box-sizing: border-box;
    width: 100%;
    height: 1000px;
}

.carousel-container {
    width: 50%;
    box-sizing: border-box;
    overflow: hidden;
    z-index: 1;
}

.carousel-slide {
    display: flex;
    position: relative;
    transition: transform 0.5s ease-in-out;
    z-index: 1;
}

.box {
    flex: 0 0 40%; /* Each item takes up 20% of the carousel width (100%/5 items) */    
    box-sizing: border-box;

    margin-top: 10px;
    margin-bottom: 10px;
    margin-right: 10px;
    height: 200px;

    background-color: coral;
    text-align: center;
    
    border-radius: 30px;

    cursor: pointer;
}
.box:hover{
    background-color: coral;
    box-shadow: 2px 0 25px 0 rgb(181, 176, 176);
    transition: transform 1s ease-in-out;
}

.prevBtn, .nextBtn {
    position: absolute;
    transform: translateY(-130px);
    background-color: white;
    box-shadow: 0 0 4px 0 slategray;
    color: black;
    border: none;
    border-radius: 100%;
    padding: 10px;
    cursor: pointer;
    z-index: 2;
}

.prevBtn {
    left: 22%;
}

.nextBtn {
    right: 22%;
}
