.form{
    display: flex;
    flex-direction: column;

    width: 200px;

    padding: 10px;
    margin: 10px;

    background-color: aliceblue;

    align-items: center;
}

.header{
    position: relative;
    top: 0;
    font-family: 'Courier New', Courier, monospace;
    padding: 10px;
}

.username{
    left: 0;
    border: 0px solid rebeccapurple;


    padding: 5px;
    margin: 5px;
    border-radius: 5px;
    box-shadow: 0 0 2px 0 rebeccapurple;
    outline: none;
}

.password{
    border: 0px solid;
    padding: 5px;
    margin: 5px;
    border-radius: 5px;
    box-shadow: 0 0 2px 0 rebeccapurple;
    outline: none;
}

.submit{
    background-color: aquamarine;
    border: 0px solid;
    cursor: pointer;
    margin: 10px;
    border-radius: 10px;
    width: 50%;
    height: 20px;
    color: white;
}

.submit:hover{
    transition: background-color 0.5s ease;
    background-color: rgb(6, 85, 59);
}

.username:hover, .password:hover{
    box-shadow: 0 0 5px 0 rebeccapurple;
}

.username:focus, .password:focus{
    border: 1px solid rebeccapurple; /* Change border color */
    box-shadow: 0 0 5px rebeccapurple; /* Optional: add a glow effect */
}
