.table-content{
    display: flex;
}

table{
    border-collapse: collapse;
    text-align: left;
}

tbody{
    margin-top: 30px;
    max-height: 300px;

    overflow: scroll;
}

td{
    font-weight: lighter;
    width: 150px;
}


thead td{
    font-weight: bold;
    background-color: orange;
    height: 30px;
}

tr:nth-child(odd) {
    font-weight: lighter;
    background-color: aliceblue;
}

.action-button > button{
    margin: 10px;
    padding: 4px;
}
