.nav-bar{
    width: 100%;
    top: 0;
    left: 0;
    background-color: black;
    opacity: 1;
    position: fixed;
    z-index: 5;
    transition: opacity 0.6s ease-out, transform 0.6s ease-out;
}

.nav-bar-menu{
    display: flex;
    flex-direction: row;
    color: white;
    list-style: none;
}

.nav-bar-item{
    padding: 10px;
}

.counter-container {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
}

.counter{
    display: flex;
    flex-direction: column;
    width: 200px;
    height: 200px;

    margin: 10px;
    padding: 10px;

    background-color: rgba(132, 165, 238, 0.8);
    border-radius: 4px;

    justify-content: center;
    text-align: center;   

    transition: opacity 0.6s ease-out, transform 1s ease-out;
    
    transform: translateY(200px);
    opacity: 0;
}

.invisible{
    transform: translateY(-100px);
    opacity: 0;
}

.visible{
    transform: translateY(0);
    opacity: 1;
}

