.main-box-1{
    height: 1000px;
    background-color: rgb(255, 183, 0);
}
.main-box-2{
    height: 1000px;
    background-color: pink;
}

.box{
    border-width: 2px;
    border-style:solid ;
    border-color: rgb(79, 41, 117);
    padding: 30px;
    display: inline-block;
    margin: 20px;
    top: 0;
}

#box3{
    /* position: static; Default position
    /* position: relative; Relative to its normal position */
    /* position: absolute; Relative to its first parent position */
    /* position: fixed; Fixed position */
    /* position: sticky; Stick to parent */
    position: sticky;
    left: 35px;
    top: 30px;
}